button {
  padding: 2px;
  margin: 10px;
  color: #80453d;
}
button[type="submit"] {
  border-radius: 12px;
}

button[type="button"]:hover {
  background-color: red;
  color: yellow;
}
button[type="submit"]:hover {
  background-color: green;
  color: yellow;
  border-radius: 12px;
}
li {
  color: black;
  font-size: medium;
  font-weight: 600;
}

#myform {
  color: #050441;
  background-color: rgb(196, 196, 245);
  position: fixed;
  top: 5%;
  left: 2%;
  border: 2px solid rgb(30, 3, 150);
  border-radius: 20px 20px;
}
::-webkit-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: blue;
  padding: 10px;
}
::-ms-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: blue;
  padding: 10px;
}
::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: blue;
  padding: 10px;
}
button[type="submit"] {
  border-color: green;
  border: 1px solid green;
  padding: 10px;
}

#myInput {
  border: 1px solid black;
  margin: 10px;
  padding: 10px;
  color: blue;
}

